.navbarlink-container {
  padding: 4px;
  text-align: center;
  margin-bottom: 15px;
}
.navbar-link-icon {
  scale: 0.8;
  padding: 2.5px 20px;
  border-radius: 100px;
  color: blue;
  cursor: pointer;
}
.navbar-link-icon:hover {
  background-color: #519657;
}
.navbar-link-icon:active {
  background-color: #b2fab4;
}
.navbar-svg-icon:hover {
  font-variation-settings: "wght" 400;
}
.navbar-svg-icon:active {
  font-variation-settings: "FILL" 1, "wght" 400, "opsz" 24;
}
.navlink-title {
  font-family: euclidCircularBMedium;
  font-size: 14px;
}
.navbar-svg-icon {
  cursor: pointer;
  color: black;
  font-family: "Material Symbols Outlined";
  font-weight: normal;
  font-style: normal;
  font-size: 40px; /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  font-variation-settings: "FILL" 0, "wght" 300, "GRAD" 0, "opsz" 40;
  opacity: 1;
}
